import { mapGetters,mapMutations,mapActions } from 'vuex'
import { calculatedPrice , addShoppingCarFun } from '../../../../api/goods/goods'
import count from '../../../../components/count/count'

export default {
	props: ['goodsId','goodsName'],
  data() {
  	return {
  		data:{}, // 存放规格数据
  		selectedSpecId:[], // 存放规格ID
  		selectedSpecName:[], // 存放规格名称
  		goodsNum:1, // 存放数量
      confirmGoodsObj: {} // 存放跳转到确认订单的商品信息
  	}
  },
  methods: {
  	/*
  	* 关闭model
  	*/
  	closeModel(){
  		this.setSpecState(false)
  	},
  	/*
  	* 默认选中第一个选项
  	*/
  	defaultOneOption(){
  		for(let i=0;i<this.specData.length;i++){
  			this.keepSpecIdClick(i,this.specData[i].specificationBeans[0].specification_id,this.specData[i].specificationBeans[0].specification_value)
  		}
  	},
  	/*
  	* 保存选中的ID
  	*/
  	keepSpecIdClick(paIndex,id,name){ // id:规格ID，name:规格名称 ，外层循环下标
  		this.selectedSpecId[paIndex] = id
  		this.selectedSpecName[paIndex] = name
      console.log(paIndex)
      console.log(this.selectedSpecId)
  		this._calculatedPrice(this.selectedSpecId)
  	},

  	/*
  	* 计算价格
  	*/
  	_calculatedPrice(id) {
  		calculatedPrice(this.goodsId,id).then((res) => {
  			console.log(res)
  			if(res.status ==='ok'){
          this.data = res.data
        }else if(res.status === 'error'){
          console.log(res.data)
        }
  		})
  	},
  	/*
  	* 加
  	*/
  	numPlus(count) {
  		this.goodsNum = count
      console.log(this.goodsNum)
  	},
  	/*
  	* 减
  	*/
  	numMinus(count) {
  		this.goodsNum = count
      console.log(this.goodsNum)
  	},
    /*
    * 加入购物车
    */
    _addShoppingCarFun() {
      addShoppingCarFun(this.goodsId,this.goodsNum,this.data.specification_id)
      .then((res) => {
        if(res.status ==='ok'){
          console.log(res.data)
          this.closeModel(false)
          this.selectPrompt({
            promptData:{ // 数据
              content:res.data,
              type:'success',
            },
            promptState:true, // 状态
            promptTime: 2000, // 显示时间
            autoClose: true //是否自动隐藏
          })
        }else if(res.status === 'error'){
          console.log(res.data)
          this.selectPrompt({
            promptData:{ // 数据
              content:res.data,
              type:'error',
            },
            promptState:true, // 状态
            promptTime: 2000, // 显示时间
            autoClose: true //是否自动隐藏
          })
        }
      }).catch((e) => {

      })
    },
  	/*
  	* 确定
  	*/
  	determineFun() {
  		console.log("确定")
      console.log(this.goodsNum)
      if(this.specType === 'car'){
        this._addShoppingCarFun()
      }else{
        console.log('立即购买')
        this.confirmGoodsObj = {
          goods_id: this.goodsId,
          goods_img: this.data.specification_img,
          goods_name: this.goodsName,
          specification_id: this.data.specification_id,
          specification_names: this.data.specification_names,
          goods_now_price: this.data.specification_price,
          num: this.goodsNum
        }
        console.log(this.confirmGoodsObj)
        sessionStorage.setItem('confirmGoodsObj',JSON.stringify(this.confirmGoodsObj))
        this.$router.push({
          path:'/confirmOrder'
        })
      }
  	},
  	...mapMutations({
  		setSpecState: 'SET_SPECSTATE'
  	}),
    ...mapActions([
      'selectPrompt'
    ])
  },
  computed:{
  	...mapGetters([
  		'specData',
  		'specType',
  		'specState',
  	])
  },
  mounted() {
  	setTimeout(() => {
      this.defaultOneOption()
    },100)
  },
  components: {
  	count
  }
}



