import * as types from '../mutations-types'

const state = {
	specData:{}, // 规格数据
	specType:null, // 选择规格类型
	specState: false, // 规格盒子状态
	goodsNum:1 //商品数量
}

const getters = {
	specData : state => state.specData,
	specType : state => state.specType,
	specState : state => state.specState,
	goodsNum : state => state.goodsNum
}

const actions = {
	selectSpec ({commit,state},{specType,specState}){
		commit(types.SET_SPECTYPE,specType),
		commit(types.SET_SPECSTATE,specState)
	}
}

const mutations = {
	[types.SET_SPECDATA](state,specData){
		state.specData = specData
	},

	[types.SET_SPECTYPE](state,specType){
		state.specType = specType
	},

	[types.SET_SPECSTATE](state,specState){
		state.specState = specState
	},

	[types.SET_GOODSNUM](state,goodsNum){
		state.goodsNum = goodsNum
	}
}

export default {
  state,
  getters,
  actions,
  mutations
}


