
import { addAddressFun } from '../../api/order/order'
import { mapActions } from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import value2name from 'vux/src/filters/value2name.js'



export default {
props:{
onRefresh: {
type: Function,
default: undefined
}
},
data () {
return {
promprObj: {
promptState:true, // 状态
promptTime: 2000, // 显示时间
autoClose: true 
},
valueTxt:[],
name:null,
mobile:null,
address_detailed:null,
address_zip_code:null,
value: ['上海市','市辖区','浦东新区'], // 选中的地区数据
addressData: ChinaAddressV4Data, // 省市区数据
showAddress: false
}
},
methods: {
/*
* model关闭事件
*/
close (state) {
this.$emit("on-close",state)
},
/*
* 新增地址
*/
_addAddressFun () {
addAddressFun({
address_mobile: this.mobile,
address_name : this.name,
address_province : this.valueTxt[0],
address_city : this.valueTxt[1],
address_country : this.valueTxt[2],
address_detailed : this.address_detailed,
address_zip_code : this.address_zip_code
})
.then((res) => {
console.log(res)
if(res.status ==='ok'){
this.promprObj.promptData = {
content:res.data,
type:'success',
}
this.selectPrompt(this.promprObj)
if (this.onRefresh) {
this.onRefresh()
}
this.close(false)
}else if(res.status === 'error'){
this.promprObj.promptData = {
content:res.data,
type:'error',
}
this.selectPrompt(this.promprObj)
console.log(res.data)
}
})
},
onShadowChange (ids, names) {
this.valueTxt = names
console.log(this.valueTxt)
},
...mapActions([
'selectPrompt'
])
},
components: {
Group,
XAddress
}
}



