import { backFun } from '../../../common/dom'
import { getGoodsDetailsFun,getShopDetailsFun,collectionFun } from '../../../api/goods/goods'
import MyBanner from '../../../components/banner/banner'
import GShop from './son/shop' 
import GoodsSpec from './son/goodsSpec'
import { mapMutations } from 'vuex'
import { mapActions } from 'vuex'

export default {
	data() {
		return {
			goodsDetails:{},
			shopDetails:{},
		}
	},
  methods: {
  	/*
  	* 获取商品详情
  	*/
  	_getGoodsDetailsFun() {
  		getGoodsDetailsFun(this.$route.query.goods_id)
  		.then((res) => {
        console.log(res)
        if(res.status ==='ok'){
          this.goodsDetails = res.data
          this.setSpecData(res.data.goodsSpecificationBeans)
          this._getShopDetailsFun()
        }else if(res.status === 'error'){
          console.log(res.data)
        }
      })
  	},
  	/*
  	* 获取店铺详情
  	*/
  	_getShopDetailsFun() {
  		getShopDetailsFun(this.goodsDetails.merchants_id)
  		.then((res) => {
  			console.log(res)
  			if(res.status === 'ok'){
          this.shopDetails = res.data
        }else if(res.status === 'error'){
          console.log(res.data)
        }
  		})
  	},
  	/*
  	* 打开规格model
  	*/
  	openSpecModelClick(type) {
  		this.selectSpec({
  			specType: type,
  			specState: true
  		})
  	},
  	/*
  	* 收藏商品
  	*/
  	collectionClick () {
  		collectionFun(this.$route.query.goods_id)
  		.then((res) => {
  			console.log(res)
  			if(res.status === 'ok'){
  				this.goodsDetails.is_collect = res.data
  				if(res.data == 1) {
  					
  				}
  			}else if(res.status === 'error'){
  				console.log(res.data)
  			}
  		})
  	},
  	_backFun() {
  		backFun()
  	},
  	...mapMutations({
  		setSpecData:'SET_SPECDATA'
  	}),
  	...mapActions([
  		'selectSpec',
  		'selectPrompt'
  	])
  },
  mounted() {
  	this._getGoodsDetailsFun();
  },
  components: {
  	MyBanner,
  	GShop,
  	GoodsSpec
  }
}



