import MyHeader from '../../../components/header/header'
import ConfirmAddress from './son/confirm_address'
import GoodsItem from '../../../components/goodsItem/goodsItem'

const confirmGoodsObj = JSON.parse(sessionStorage.getItem("confirmGoodsObj"))
const memberInfo = JSON.parse(sessionStorage.getItem("memberInfo"))

export default {
	data () {
		return {
			data: JSON.parse(sessionStorage.getItem("confirmGoodsObj")),
			paymentState: true,
			orderJson: {
				member_id: memberInfo.uid, // 用户id
		    address_id: "", // 地址ID
		    deduct_integral_value: "", // 抵扣积分
		    coupon_ids: "", // 优惠券id,有多个用‘,’分隔
		    orderBeans:[{
		      merchants_id: JSON.parse(sessionStorage.getItem("confirmGoodsObj")).merchants_id, // 商家ID
		      order_type: "", // 订单类型
		      order_remark: "", // 订单备注
		      member_group_id: "", // 团购下单 需传值 用户开团主键
		      orderGoodsBeans:[{
		        goods_id: JSON.parse(sessionStorage.getItem("confirmGoodsObj")).goods_id, // 商品ID
		        specification_id: JSON.parse(sessionStorage.getItem("confirmGoodsObj")).specification_id, // 规格ID
		        goods_num: JSON.parse(sessionStorage.getItem("confirmGoodsObj")).num, //购买数量
		        goods_group_id: ""  // 团购下单ID
		      }]
		    }]
			}
		}
	},
	methods: {
		/*
		* 获取地址ID
		*/
		getAddressId (id) {
			console.log(id)
			this.orderJson.address_id = id
		},
		/*
		* 提交订单
		*/
		submitOrder () {
			console.log(this.orderJson)
		}
	},
  components: {
  	MyHeader,
  	ConfirmAddress,
  	GoodsItem
  }
}



