import axios from 'axios'

/*
* 获取首页banner图
*/
export function getBannerFun () {
	return axios.get('api/index/banner_list')
  .then((res) => {
  	return Promise.resolve(res.data)
  	// return res.data
  })
  .catch((e) => {
  	return Promise.resolve(e)
  });
}

/*
* 获取商品分类
*/
export function getGoodsClassFun () {
	return axios.get('api/Home/home_class')
	.then((res) => {
		return Promise.resolve(res.data)
	})
	.catch((e) => {
  	return Promise.resolve(e)
  });
}

/*
* 获取首页推荐信息
*/
export function getRecommendFun () {
	return axios.get('api/Home/dress')
	.then((res) => {
		return Promise.resolve(res.data)
	})
	.catch((e) => {
  	return Promise.resolve(e)
  });
}


