// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import VueLazyload from 'vue-lazyload'

Vue.use(VueLazyload, {
  error: './assets/icon/error.png', // 图片路径错误时加载图片
  loading: './assets/icon/loading.png' // 预加载图片
})

Vue.config.productionTip = false

/*
* 实现登录后跳转方法
*/
Vue.prototype.goBeforeLoginUrl = () => {
  let url = sessionStorage.getItem('beforeLoginUrl')
  console.log(url)
  if(!url || url.indexOf('/author') != -1){
    router.push('/home')
  }else{
    if(url == '/'){
      url = '/home'
    }
    router.push(url)
    sessionStorage.setItem('beforeLoginUrl', '')
  }
}

/*
* 路由钩子函数
* url进入网站都会先触发router.beforeEach钩子
*/
router.beforeEach((to, from, next) => {
  if(to.path == '/author' && sessionStorage.getItem('memberInfo')){
    // 用户使用后退返回到授权页，则默认回到首页
    next('/home')
    return false
  }
  /*
  * 判断sessionStorage里有没有用户信息
  * 并且是否是微信授权页
  */
  if(!sessionStorage.getItem('memberInfo') && to.path !='/author'){
    sessionStorage.setItem('beforeLoginUrl', to.fullPath) // 保存用户进入的url
    next('/author') // 进入到微信授权页
    return false
  }else {
    next()
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



