import { mapGetters } from 'vuex'
import { mapActions } from 'vuex'

export default {
  data () {
  	return {
	  	time: null,
  	}
  },
  methods: {
  	countdown () {
	    if (this.autoClose) {
        console.log(this.autoClose,this.promptTime)
	      this.time = setTimeout(() => {
	        this.close()
	      }, this.promptTime)
	    }
	  },
  	close () {
  		this.selectPrompt({
        promptData:{}, // 数据
        promptState:false, // 状态
        promptTime:2000, // 显示时间
        autoClose:true //是否自动隐藏
      })
  	},
    /*
    * 判断是显示还是隐藏
    */
    isShow () {
      if(this.promptState){

      }
    },
    ...mapActions([
      'selectPrompt'
    ])
  },
  watch: {
  	promptState () {
      window.clearTimeout(this.time)
	    this.countdown()
	  }
  },
  computed:{
    setStyle () {
      return {
        color: this.promptData.textColor || '#fff',
        background: this.promptData.backgroundColor ? this.promptData.backgroundColor : this.promptData.type === 'error' ? 'red' : '#21e7b6'
      }
    },
    ...mapGetters([
      'promptData',
      'promptState',
      'promptTime',
      'autoClose'
    ])
  },
  mounted () {
    // this.countdown()
  }
}



