import * as types from '../mutations-types'

const state = {
	memberInfo: {}, // 用户信息
}

const getters = {
	memberInfo : state => state.memberInfo
}

const actions = {

}

const mutations = {
	[types.SET_MEMBERINFO] (state,memberInfo) {
		state.memberInfo = memberInfo
	}
}

export default {
  state,
  getters,
  actions,
  mutations
}


