import * as types from '../mutations-types'

const state = {
	promptData:{}, // 数据
	promptState:false, // 状态
	promptTime: 2000, // 显示时间
	autoClose:true //是否自动隐藏
}

const getters = {
	promptData : state => state.promptData,
	promptState : state => state.promptState,
	promptTime : state => state.promptTime,
	autoClose : state => state.autoClose
}

const actions = {
	selectPrompt ({commit,state},{promptData,promptState,promptTime,autoClose}){
		commit(types.SET_PROMPTDATA,promptData),
		commit(types.SET_PROMPTSTATE,promptState),
		commit(types.SET_PROMPTTIME,promptTime),
		commit(types.SET_AUTOCLOSE,autoClose)
	}
}

const mutations = {
	[types.SET_PROMPTDATA](state,promptData){
		state.promptData = promptData
	},

	[types.SET_PROMPTSTATE](state,promptState){
		state.promptState = promptState
	},

	[types.SET_PROMPTTIME](state,promptTime){
		state.promptTime = promptTime
	},

	[types.SET_AUTOCLOSE](state,autoClose){
		state.autoClose = autoClose
	}
}

export default {
  state,
  getters,
  actions,
  mutations
}


