import axios from 'axios'
import qs from 'qs'

axios.defaults.withCredentials = true 
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded; charset=UTF-8'

const memberInfo = JSON.parse(sessionStorage.getItem("memberInfo"))
console.log(memberInfo)

/*
* 获取默认地址
*/
export function getDefaultAddressFun (){
	return axios.post(
		'api/Address/queryDefaultAddress',
		qs.stringify({
			uid:memberInfo.uid,
			token:memberInfo.token
		})
	).then((res) => {
		return Promise.resolve(res.data)
	})
	.catch((e) => {
		return Promise.resolve(e)
	})
}

/*
* 新增地址
*/
export function addAddressFun (addressInfo){
	console.log(addressInfo)
	return axios.post(
		'api/Address/insertAddress',
		qs.stringify({
			uid:memberInfo.uid,
			token:memberInfo.token,
      address_mobile: addressInfo.address_mobile, // 手机号
      address_name: addressInfo.address_name, //姓名
      address_province: addressInfo.address_province, //省
      address_city: addressInfo.address_city, //市
      address_country: addressInfo.address_country, //区
      address_detailed: addressInfo.address_detailed, //详细地址 
      address_zip_code: addressInfo.address_zip_code//邮编
		})
	).then((res) => {
		return Promise.resolve(res.data)
	})
	.catch((e) => {
		return Promise.resolve(e)
	})
}


