import axios from 'axios'
import qs from 'qs'

axios.defaults.withCredentials = true 
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded; charset=UTF-8'

const memberInfo = JSON.parse(sessionStorage.getItem("memberInfo"))
console.log(memberInfo)
/*
* 获取商品列表
*/
export function getGoodsListFun (type,page,class_uuid) {
	return axios.post(
		'api/Mall/searchGoods',
		qs.stringify({
			name:'',
			class_uuid:class_uuid,
			p:page,
			type:type,
			pagesize : 10
		})
	).then((res) => {
		return Promise.resolve(res.data)
	})
	.catch((e) => {
		return Promise.resolve(e)
	})
}

/*
* 获取商品详情
*/
export function getGoodsDetailsFun (goods_id) {
	return axios.post(
		'api/Mall/goods_info',
		qs.stringify({
			uid:memberInfo.uid,
			token:memberInfo.token,
			goods_id:goods_id
		})
	).then((res) => {
		return Promise.resolve(res.data)
	})
	.catch((e) => {
		return Promise.resolve(e)
	})
}

/*
* 收藏or取消收藏
*/
export function collectionFun (goods_id) {
	return axios.post(
		'api/Mall/goods_collect',
		qs.stringify({
			uid:memberInfo.uid,
			token:memberInfo.token,
			goods_id:goods_id
		})
	).then((res) => {
		return Promise.resolve(res.data)
	})
	.catch((e) => {
		return Promise.resolve(e)
	})
}

/*
* 获取店铺信息
*/
export function getShopDetailsFun (merchants_id) {
	return axios.post(
		'api/Mall/merchants_info',
		qs.stringify({
			uid:memberInfo.uid,
			token:memberInfo.token,
			merchants_id:merchants_id  //商品member_id
		})
	).then((res) => {
		return Promise.resolve(res.data)
	})
	.catch((e) => {
		return Promise.resolve(e)
	})
}

/*
* 计算规格价格
*/
export function calculatedPrice (goods_id,specification_ids){
	return axios.post(
		'api/Mall/get_specification',
		qs.stringify({
			goods_id:goods_id,
			specification_ids:specification_ids.join(",") //商品member_id
		})
	).then((res) => {
		return Promise.resolve(res.data)
	})
	.catch((e) => {
		return Promise.resolve(e)
	})
}

/*
* 加入购物车
*/
export function addShoppingCarFun (goods_id,num,specification_id){
	console.log(goods_id,num,specification_id)
	return axios.post(
		'api/Mall/insertShopCar',
		qs.stringify({
			uid: memberInfo.uid,
      token: memberInfo.token,
      goods_id : goods_id,
      goods_num : num,
      specification_id : specification_id // 组合规格ID
		})
	).then((res) => {
		return Promise.resolve(res.data)
	})
	.catch((e) => {
		return Promise.resolve(e)
	})
}











